import React, { Component } from "react";
import { Carousel } from "react-responsive-carousel";

function Carsoul() {
  return (
  <div dir='ltr'  className=" 100-vh  shadow border border-3 border-dark my-2 ">
      <Carousel  autoPlay={false} infiniteLoop={true} thumbWidth={'220px'} width={'100%'}>
        <div>
          <img style={{ height : '80vh'}} className=" " src="https://img.staticmb.com/mbcontent//images/uploads/2022/12/Most-Beautiful-House-in-the-World.jpg" />
          <p className="legend">0100100100100</p>
        </div>
        <div>
          <img style={{ height : '80vh'}}  src="https://img.staticmb.com/mbcontent//images/uploads/2022/12/Most-Beautiful-House-in-the-World.jpg" />
          <p className="legend">0100100100100</p>
        </div>
        <div>
          <img style={{ height : '80vh'}}  src="https://img.staticmb.com/mbcontent//images/uploads/2022/12/Most-Beautiful-House-in-the-World.jpg" />
          <p className="legend">0100100100100</p>
        </div>
      </Carousel>
  </div>
  );
}

export default Carsoul;

