import { Breadcrumbs } from "@mui/material";
import { Link } from "react-router-dom";
import HomeIcon from "@mui/icons-material/Home";
import PinDropIcon from "@mui/icons-material/PinDrop";
import Header from "../component/Header";
import Carditem from "../component/Ui/Carditem";
import Typography from "@mui/material/Typography";
import Pagination from "@mui/material/Pagination";
import Stack from "@mui/material/Stack";
import React from "react";

function Rent() {
  const [page, setPage] = React.useState(1);
  const handleChange = (event, value) => {
    setPage(value);
  };
  function handleClick(event) {
    event.preventDefault();
    console.info("You clicked a breadcrumb.");
  }
  return (
    <>
      <div className=" container-fluid">
        <div className=" row">
          <nav className="col-12  ">
            <div role="presentation" onClick={handleClick}>
              <Breadcrumbs
                className=" d-flex justify-content-start align-items-center m-5"
                separator="/"
                aria-label="breadcrumb"
              >
                <Link
                  className="text-black"
                  underline="hover"
                  sx={{ display: "flex", alignItems: "center" }}
                  color="black"
                  href="/"
                >
                  <HomeIcon sx={{ mr: 0.5 }} fontSize="inherit" />
                  home
                </Link>
                <Link
                  className="text-black"
                  underline="hover"
                  sx={{ display: "flex", alignItems: "center" }}
                  color="black"
                  href="/"
                >
                  <HomeIcon sx={{ mr: 0.5 }} fontSize="inherit" />
                  rent
                </Link>
              </Breadcrumbs>
            </div>
          </nav>
          <div className=" col-12">
            <Header />
          </div>
          <div className=" col-12 bg-dark my-4 text-center text-light py-3 ">
            <h2> Similar Apartments For Rent in Maadi , Maadi Sarayat</h2>
          </div>
          <div className="col-12">
            <div className="col-md-4">
              <Carditem />
            </div>
          </div>
          <div className="col-12">
           <div className=" d-flex justify-content-center   text-center">
             <Stack spacing={2}>
               <Typography>Page: {page}</Typography>
               <Pagination count={10} page={page} onChange={handleChange} />
             </Stack>
           </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Rent;
