import { useEffect, useState } from "react";
import { Link, NavLink } from "react-router-dom";
import * as React from "react";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import TextField from "@mui/material/TextField";
import { Button } from "bootstrap";
import Modulalist from "./Ui/Modulalist";

function Secnav() {
  const [navbar, setNavbar] = useState(false);
  const [inputValue, setInputValue] = React.useState("");
  const [name, setName] = React.useState("");

  const changeBackground = () => {
    if (window.scrollY >= 95) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", changeBackground);
  }, []);
  return (
    <>
      <nav className={`   ${navbar ? "fixed-top  " : " "}`}>
        <Navbar bg="light" expand="lg">
          <Container>
            <Nav className="w-100 ">
              <nav className=" w-100 d-md-flex  justify-content-around align-items-center ">
                <NavLink className="text-danger "> 01221409530</NavLink>
                <NavLink className="text-danger ">
                  {" "}
                  info@housepointegypt.com
                </NavLink>
                  {" "}
                  <div className=" py-3">
                    <a href className="me-4 text-reset">
                      <i className="fab fa-facebook-f" />
                    </a>
                    <a href className="me-4 text-reset">
                      <i className="fab fa-twitter" />
                    </a>
                    <a href className="me-4 text-reset">
                      <i className="fab fa-google" />
                    </a>
                    <a href className="me-4 text-reset">
                      <i className="fab fa-instagram" />
                    </a>
                    <a href className="me-4 text-reset">
                      <i className="fab fa-linkedin" />
                    </a>
                    <a href className="me-4 text-reset">
                      <i className="fab fa-youtube" />
                    </a>
                  </div>
                  <Modulalist />
              </nav>
            </Nav>
          </Container>
        </Navbar>

        <Navbar bg="light" expand="lg">
          <Container>
            <Navbar.Brand>house point eg</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto w-100 d-flex justify-content-around align-items-center">
                <NavLink to={"/"} className="text-danger ">
                  home
                </NavLink>
                <NavLink to={"/rent"} className="text-danger ">
                  for rent
                </NavLink>
                <NavLink to={"/sell"} className="text-danger ">
                  for sell
                </NavLink>
                <NavLink to={"/blog"} className="text-danger ">
                  blog
                </NavLink>
                <NavLink className="text-danger ">contact us</NavLink>

                <TextField
                  id="outlined-controlled"
                  label="search"
                  value={name}
                  onChange={(event) => {
                    setName(event.target.value);
                  }}
                />
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
      </nav>
    </>
  );
}

export default Secnav;
