import React from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBIcon,
  MDBBtn,
  MDBRipple,
} from "mdb-react-ui-kit";
import ChaletRoundedIcon from "@mui/icons-material/ChaletRounded";
import LocalHotelRoundedIcon from "@mui/icons-material/LocalHotelRounded";
import BathtubRoundedIcon from "@mui/icons-material/BathtubRounded";
import ChairRoundedIcon from "@mui/icons-material/ChairRounded";
function Carditem() {
  return (
    <MDBContainer fluid className="my-5 text-center">
      <MDBRow>
        <MDBCol className="mb-4">
          <MDBCard>
            <MDBRipple
              rippleColor="light"
              rippleTag="div"
              className="bg-image rounded hover-zoom"
            >
              <MDBCardImage
                src="https://cdn-bnokp.nitrocdn.com/QNoeDwCprhACHQcnEmHgXDhDpbEOlRHH/assets/images/optimized/rev-d78d19d/online-decorating/wp-content/uploads/2020/07/House-interior-design-of-a-chic-living-room.jpeg"
                fluid
                className="w-100"
                alt="ayman"
              />
              <a>
                <div className="mask">
                  <div class="d-flex justify-content-around  ">
                    <h5 className="">
                      <span className="badge bg-light text-black ms-2">
                        for rent
                      </span>
                      <span className="badge bg-light text-black ms-2">
                        appartment
                      </span>
                      <span className="badge bg-light text-black ms-2 mt-3">
                        maadi,maadi saryat
                      </span>
                      <span className="badge bg-light text-black ms-2 mt-3">
                        ref:123
                      </span>
                    </h5>
                  </div>
                </div>
                <div className="hover-overlay">
                  <div
                    className="mask"
                    style={{ backgroundColor: "rgba(251, 251, 251, 0.15)" }}
                  ></div>
                </div>
              </a>
            </MDBRipple>
            <MDBCardBody>
              <a href="#!" className="text-reset">
                <h1 className="card-title mb-3 main-color h3">
                  2 Bedrooms Flat For Rent In Sarayat El Maadi
                </h1>
              </a>
              <a href="#!" className="text-reset">
                {/* <p>appartment</p> */}
              </a>
              <h6 className="mb-3">
                {/* <s>$61.99</s> */}
                <strong className="ms-2 h4 text-danger">
                  35,000 eg / month
                </strong>
              </h6>
              <hr />
              <div className=" row text-start ">
                <div className="col-6 h5 ">
                  <ChaletRoundedIcon fontSize="large"></ChaletRoundedIcon>
                  <span> 120 m</span>
                </div>
                <div className="col-6  h5 ">
                  <LocalHotelRoundedIcon fontSize="large"></LocalHotelRoundedIcon>{" "}
                  2 bed rooms
                </div>
                <div className="col-6 h5">
                  <BathtubRoundedIcon fontSize="large"></BathtubRoundedIcon> 2
                  bathroom
                </div>
                <div className="col-6 h5">
                  <ChairRoundedIcon fontSize="large"></ChairRoundedIcon>{" "}
                  furnished
                </div>
              </div>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  );
}

export default Carditem;
